import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Global} from '../../../../Global';
import {Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DepartmentService {
  private baseUrl = this.global.baseUrl + '/api/department/';
  private saveDepartmentUrl = this.baseUrl + 'save';
  private getAllUrl = this.baseUrl + 'get-all';
  constructor(private http: HttpClient, private global: Global) { }

  saveItem(item): Observable<any> {
    return this.http.post<any>(this.saveDepartmentUrl, item);
  }

  getAll(): Observable<any> {
    return this.http.get<any>(this.getAllUrl);
  }


}
