import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Global} from '../../../../Global';
import {Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PriorityService {
  private baseUrl = this.global.baseUrl + '/api/priority/';
  private savePriorityUrl = this.baseUrl + 'save';
  private getAllUrl = this.baseUrl + 'get-all';
  constructor(private http: HttpClient, private global: Global) { }

  savePriority(priority): Observable<any> {
    return this.http.post<any>(this.savePriorityUrl, priority);
  }

  getAll(): Observable<any> {
    return this.http.get<any>(this.getAllUrl);
  }
}
