import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {Global} from '../../Global';

@Injectable({
  providedIn: 'root'
})
export class TicketService {
  private baseUrl = this.global.baseUrl + '/api/ticket/';
  private saveTicketUrl = this.baseUrl + 'save';
  private getAllUrl = this.baseUrl + 'get-all';
  private getItemUrl = this.baseUrl + 'get-item';
  private getByUserUrl = this.baseUrl + 'get-by-user';
  constructor(private http: HttpClient, private global: Global) { }

  saveTicket(ticket): Observable<any> {
    return this.http.post<any>(this.saveTicketUrl, ticket);
  }

  getAll(): Observable<any> {
    return this.http.get<any>(this.getAllUrl);
  }

  getTicket(id: any): Observable<any> {
  return  this.http.get<any>(this.getItemUrl,
    { params: {
      id: id
    }});
  }

  getTicketByUser(): Observable<any> {
	  console.log("called.subscribe()");
	  

    return this.http.get<any>(this.getByUserUrl);
  }
}
