import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Global} from '../../../../Global';
import {Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CategoryService {
  private baseUrl = this.global.baseUrl + '/api/category/';
  private saveCategoryUrl = this.baseUrl + 'save';
  private getAllUrl = this.baseUrl + 'get-all';
  constructor(private http: HttpClient, private global: Global) { }

  saveCategory(category): Observable<any> {
    return this.http.post<any>(this.saveCategoryUrl, category);
  }

  getAll(): Observable<any[]> {
    return this.http.get<any[]>(this.getAllUrl);
  }
}
