import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Global} from '../../../../Global';
import {Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class StatusService {
  private baseUrl = this.global.baseUrl + '/api/status/';
  private saveStatusUrl = this.baseUrl + 'save';
  private getAllUrl = this.baseUrl + 'get-all';
  constructor(private http: HttpClient, private global: Global) { }

  saveStatus(status): Observable<any> {
    return this.http.post<any>(this.saveStatusUrl, status);
  }

  getAll(): Observable<any[]> {
    return this.http.get<any[]>(this.getAllUrl);
  }
}
