import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Global} from '../../../../Global';
import {Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private baseUrl = this.global.baseUrl + '/api/admin/user/';
  private saveStaffUrl = this.baseUrl + 'save/staff';
  private getAllUrl = this.baseUrl + 'get-all';
  private saveItemUrl = this.baseUrl + 'save';
  private setDepartmentUrl = this.baseUrl + 'set-department';
  constructor(private http: HttpClient, private global: Global) { }

  public getAll(): Observable<any> {
    return this.http.get<any>(this.getAllUrl);
  }

  saveUser(item): Observable<any> {
    return this.http.post<any>(this.saveItemUrl, item);
  }

  saveStaff(item): Observable<any> {
    return this.http.get<any>(this.saveStaffUrl, { params: {
        firstName: item.firstName,
        lastName: item.lastName,
        userName: item.email
      }});
  }

  setDepartment(userName, departmentName): Observable<any> {
    return this.http.get<any>(this.setDepartmentUrl, { params: {
        userName: userName,
        departmentName: departmentName
      }
    });
  }
}
